// Colors
$label-colour: #bbb;
$disabled-colour: #ddd;
$toggle-colour: #4CAF50;
$white: #fff;
$focus-color: #ff0;

.toggle-outer {
    margin-top: 20px;
}

.toggle {
    position: relative;
    margin-right: 10px;
    width: 75px;
    display: inline-block;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-align: left;

    &-checkbox {
        display: none;
    }

    &-label {
        display: block;
        overflow: hidden;
        cursor: pointer;
        border: 0 solid $label-colour;
        border-radius: 20px;
        margin: 0;

        &:focus {
            outline: none;

            >span {
                box-shadow: 0 0 2px 5px red;
            }
        }

        >span:focus {
            outline: none;
        }
    }

    &-inner {
        display: block;
        width: 200%;
        margin-left: -100%;
        transition: margin 0.3s ease-in 0s;

        &:before,
        &:after {
            display: block;
            float: left;
            width: 50%;
            height: 34px;
            padding: 0;
            line-height: 34px;
            font-size: 14px;
            color: white;
            font-weight: bold;
            box-sizing: border-box;
        }

        &:before {
            content: attr(data-yes);
            text-transform: uppercase;
            padding-left: 10px;
            background-color: $toggle-colour;
            color: $white;
        }
    }

    &-inner:after {
        content: attr(data-no);
        text-transform: uppercase;
        padding-right: 10px;
        background-color: $label-colour;
        color: $white;
        text-align: right;
    }

    &-switch {
        display: block;
        width: 24px;
        margin: 5px;
        background: $white;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 40px;
        border: 0 solid $label-colour;
        border-radius: 20px;
        transition: all 0.3s ease-in 0s;
    }

    &-checkbox:checked+&-label {
        .toggle-inner {
            margin-left: 0;
        }

        .toggle-switch {
            right: 0px;
        }
    }
}